// Used to style examples

body {
    padding: 32px;
}

.block {
    margin-bottom: 32px;
}

.colors {
    div {
        display: inline-block;
        width: 75px;
        height: 75px;

        &.primary-faded {background-color: $primary-faded;}
        &.primary-light {background-color: $primary-light;}
        &.primary {background-color: $primary;}
        &.primary-dark {background-color: $primary-dark;}

        &.secondary-faded {background-color: $secondary-faded;}
        &.secondary-light {background-color: $secondary-light;}
        &.secondary {background-color: $secondary;}
        &.secondary-dark {background-color: $secondary-dark;}

        &.grey-light {background-color: $grey-light;}
        &.grey {background-color: $grey;}
        &.grey-dark {background-color: $grey-dark;}
    }
}

.buttons {
    button {
        margin-bottom: 4px;
    }

    .normal-states {
        margin-bottom: 8px;
    }
}

.input {
    .states-input-test {
        margin-top: 32px;

        .states-input-buttons {
            text-align: center;
            margin-top: 9px;
        }
    }
}
